const CheckoutValidation = () => {
    let errorArr = {
        firstName:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }],
        lastName:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }],
        mobile:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }],
        address:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }],
        city:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }],
        zip:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }],
        email:[{
            theName: '',
            hasError: false,
            errorMsg: ""
        }]
    };
    let Validations = [{
            field: 'firstName',
            errors:[{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            },{
                errorName: 'maxLength',
                active: true,
                errorMsg: 'is not correct max length'
            }]
        },{
            field: 'lastName',
            errors:[{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            },{
                errorName: 'maxLength',
                active: false,
                errorMsg: 'is not correct max length'
            }]
        },{
            field: 'address',
            errors:[{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            }]
        },{
            field: 'city',
            errors:[{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            }]
        },{
            field: 'zip',
            errors:[{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            }]
        },{
            field: 'mobile',
            errors:[{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            }]
        },{
            field: 'email',
            errors:[{
                errorName: 'maxLength',
                active: false,
                errorMsg: 'is not correct max length'
            },{
                errorName: 'email',
                active: true,
                errorMsg: 'is not correct EMAIL'
            },{
                errorName: 'required',
                active: true,
                errorMsg: 'It is required field'
            }]
    }]
    var active, field, errorName, fieldLength, fieldValue, newErrorMsg;
    var regexEmail = /^[^@\s]+@[^@\s]+\.[^@\s]+$/;
{/*init errors array*/}
    for(var i=0;i<Validations.length;i++){
        for(var j=0;j<Validations[i].errors.length;j++){
            field =Validations[i].field;
            errorArr[field][0].hasError = false
            errorArr[field][0].errorMsg = ''
        }
    }
    for(var i=0;i<Validations.length;i++){
        for(var j=0;j<Validations[i].errors.length;j++){
            active = Validations[i].errors[j].active;
            field =Validations[i].field;
            errorName = Validations[i].errors[j].errorName;
            newErrorMsg = Validations[i].errors[j].errorMsg;
            fieldValue = document.getElementById(Validations[i].field).value;
            fieldLength = document.getElementById(field).value.length;
            if(active){
                //console.log("Για το πεδίο " + field + ", υπάρχει ο κανόνας για: " + errorName)
                if(errorName == "required"){
                    if(fieldLength<1){
                        errorArr[field][0].hasError = true
                        errorArr[field][0].errorMsg = newErrorMsg
                        //console.log(field + " " + errorName)
                    }else{
                        //errorArr[field][0].hasError = false
                        //errorArr[field][0].errorMsg = ''
                    }
                }else if(errorName == "maxLength"){
                    if(fieldLength>20){
                        errorArr[field][0].hasError = true
                        errorArr[field][0].errorMsg = newErrorMsg
                        //console.log(field + " " + errorName)
                    }else{
                        //errorArr[field][0].hasError = false
                        //errorArr[field][0].errorMsg = ''

                    }
                }else if(errorName == "email"){
                    if(!regexEmail.test(fieldValue)){
                        errorArr[field][0].hasError = true
                        errorArr[field][0].errorMsg = newErrorMsg
                        //console.log(field + " " + errorName) 
                    }else{
                        //errorArr[field][0].hasError = false
                        //errorArr[field][0].errorMsg = ''
                    }
                }
            }
        }
    }
    return errorArr

}

export default CheckoutValidation;