import React, {useState, useEffect} from 'react';
import '../styles/Variables.scss';
import '../styles/Checkout.scss';
import 'bootstrap-icons/font/bootstrap-icons.css';
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import {useCart} from "../context/CartContext";
import CheckoutValidation from '../Components/Validations/CheckoutValidations';

const Checkout = () =>{
    const {setOpenCart, billTotal, setIsCheckout, thecart} = useCart()
    const [deliveryOption, setDeliveryOption] = useState('');
    const [paymentOption, setPaymentOption] = useState('');
    const [info, setInfo] = useState({})
    const [errors, setErrors] = useState(null)

    useEffect(() => {
      if(errors!=null){
        console.log(errors)
        console.log("firstname error: " + errors.firstName[0].hasError)
      }
    }, [errors])
    const handleChangeDelivery = (event) => {
        setDeliveryOption(event.target.value);
    };
    const handleChangePayment = (event) => {
        setPaymentOption(event.target.value);
    };

    const leaveFromCheckout = () => {
      setIsCheckout(false)
      setOpenCart(false)
    }
    const toggleShowBrief = () =>{
      let brief = document.getElementsByClassName('order-brief-wrapper')[0];
      brief.classList.toggle("closed-brief");
    }
    const handleSubmit = () =>{
      let checkoutValidationsArray = CheckoutValidation()
      setErrors(checkoutValidationsArray)
      setInfo({
        firstName: document.getElementById('firstName').value,
        lastName: document.getElementById('lastName').value,
        email: document.getElementById('email').value,
        mobile: document.getElementById('mobile').value,
        address: document.getElementById('address').value,
        city: document.getElementById('city').value,
        zip: document.getElementById('zip').value,
        mobile: document.getElementById('firstName').value,
        deliveryOption: document.getElementById('delivery-option').value,
        paymentMethod: document.getElementById('payment-option').value,
        comments: document.getElementById('comments').value
      })
    }
    return(
        <React.Fragment>
            <div className="checkout-wrapper">

            <div className="order-brief-wrapper">
              <div className="order-brief-title" onClick={toggleShowBrief}>Παραγγελία</div>
              <div className="order-brief-container">
              {thecart.map((item, index) =>{
                return (
                <div key={index}  className="checkout-item">
                  <div  className="checkout-item-posotita"><b>{item.posotita}</b></div>
                  <div  className="checkout-item-title">{item.title}</div>
                  <div  className="checkout-item-price"><b>{item.price}&euro;</b></div>
                </div>)
                })
              }
               <div  className="checkout-item last-total">
                  <div  className="checkout-item-posotita"></div>
                  <div  className="checkout-item-title">Σύνολο: </div>
                  <div  className="checkout-item-price"><b>{billTotal()}&euro;</b></div>
                </div>
              </div>
              </div>



                <div className="checkout-container">
                <div className="checkout-title">Ολοκληρωση Παραγγελίας</div>
                <form onSubmit={handleSubmit}>
                <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="firstName"
            name="firstName"
            label="Όνομα"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'firstName',
                form: {
                  autoComplete: 'off',
                },
              }}/>
              {errors!=null&&errors.firstName[0].hasError&&<div className="error-msg">{errors.firstName[0].errorMsg}</div>}
          </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="lastName"
            name="lastName"
            label="Επώνυμο"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'lastName',
                form: {
                  autoComplete: 'off',
                },
              }} />
              {errors!=null&&errors.lastName[0].hasError&&<div className="error-msg">{errors.lastName[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="mobile"
            name="mobile"
            fullWidth
            label="Τηλέφωνο"
            variant="outlined"
            inputProps={{
              autoComplete: 'mobile',
                form: {
                  autoComplete: 'off',
                },
              }} />
              {errors!=null&&errors.mobile[0].hasError&&<div className="error-msg">{errors.mobile[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="email"
            name="email"
            label="Email"
            fullWidth
            label="Email"
            variant="outlined"
            inputProps={{
              autoComplete: 'Email',
                form: {
                  autoComplete: 'off',
                },
              }} />
              {errors!=null&&errors.email[0].hasError&&<div className="error-msg">{errors.email[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
        <TextField
          required={true}
            id="address"
            name="address"
            label="Διεύθυνση"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'address',
                form: {
                  autoComplete: 'off',
                },
              }} />
              {errors!=null&&errors.address[0].hasError&&<div className="error-msg">{errors.address[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={6} sm={4}>
        <TextField
          required={true}
            id="city"
            name="city"
            label="Πόλη"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'city',
                form: {
                  autoComplete: 'off',
                },
              }} />
              {errors!=null&&errors.city[0].hasError&&<div className="error-msg">{errors.city[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={6} sm={2}>
        <TextField
          required={true}
            id="zip"
            name="zip"
            label="Zip"
            fullWidth
            label="Zip"
            variant="outlined"
            inputProps={{
              autoComplete: 'zip',
                form: {
                  autoComplete: 'off',
                },
              }} />
              {errors!=null&&errors.zip[0].hasError&&<div className="error-msg">{errors.zip[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
        <FormControl variant="outlined" fullWidth sx={{ minWidth: 120 }}>
            <InputLabel id="delivery-option" 
          required={true}>Τρόπος Αποστολής</InputLabel>
            <Select
            labelId="delivery-option"
            id="delivery-option-standard"
            value={deliveryOption}
            onChange={handleChangeDelivery}
            label="Τρόπος Αποστολής"
            >
            {/*<MenuItem value="">
                <em>None</em>
            </MenuItem>*/}
            <MenuItem value={10}>Αποστολή στο σπίτι</MenuItem>
            <MenuItem value={20}>Παραλαβή από το κατάστημα</MenuItem>
            </Select>
        </FormControl>
        </Grid><Grid item xs={12} sm={6}>
        <FormControl variant="outlined" fullWidth sx={{ minWidth: 120 }}>
            <InputLabel id="payment-option"
          required={true}>Τρόπος Πληρωμής</InputLabel>
            <Select
            labelId="payment-option"
            id="payment-option-standard"
            value={paymentOption}
            onChange={handleChangePayment}
            label="Τρόπος Πληρωμής"
            >
            <MenuItem value={30}>Μετρητά</MenuItem>
            <MenuItem value={40}>Με κάρτα</MenuItem>
            </Select>
        </FormControl>
        </Grid>
      <Grid item xs={12}>
        <TextField
            id="comments"
            name="comments"
            label="Σχόλια"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'comments',
                form: {
                  autoComplete: 'off',
                },
              }}  />
        </Grid>
      </Grid>
      <div className="checkout-button-container">
        <Button variant="contained" style={{backgroundColor: '#efefef', color: '#000', margin: '10px 0 0', width: '40%', padding: '10px', boxShadow: '0 0'}} onClick={leaveFromCheckout}>Επιστροφη</Button>
        <Button variant="contained" onClick={handleSubmit} style={{backgroundColor: '#9cc094', color: '#fff', margin: '10px 0 0', width: '40%', padding: '10px', boxShadow: '0 0'}}>Ολοκληρωση</Button>
      </div>    
      </form>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Checkout;